import React from 'react';
import Chatkit from '@pusher/chatkit';
import MessageList from './MessageList';
import SendMessageForm from './SendMessageForm';
import TypingIndicator from './TypingIndicator';
import WhosOnlineList from './WhosOnlineList';

class Chatscreen extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            currentUser: {},
            currentRoom: {},
            usersWhoAreTyping: [],
            messages: []
        }
        this.sendMessage = this.sendMessage.bind(this);
        this.sendTypingEvent = this.sendTypingEvent.bind(this);
    }

    sendMessage(text) {
        this.state.currentUser.sendMessage({
            text,
            roomId: this.state.currentRoom.id,
        })
    }

    sendTypingEvent() {
        this.state.currentUser
            .isTypingIn({ roomId: this.state.currentRoom.id })
            .catch(error => console.error(error))
    }

    componentDidMount() {

        const chatManager = new Chatkit.ChatManager({
            instanceLocator: "v1:us1:311aa8b8-2ce9-435a-b289-991ed13030e6",
            userId: this.props.currentUsername,
            tokenProvider: new Chatkit.TokenProvider({
                url: 'http://basic-chat.herokuapp.com/authenticate'
            })
        })

        chatManager
            .connect()
            .then(currentUser => {
                this.setState({ currentUser });
                return currentUser.subscribeToRoom({
                    roomId: 19375106,
                    messageLimit: 100,
                    hooks: {
                        onNewMessage: message => {
                            this.setState({
                                messages: [...this.state.messages, message]
                            })
                        },
                        onUserStartedTyping: user => { 
                            this.setState({
                                usersWhoAreTyping: [...this.state.usersWhoAreTyping, user.name]
                            })
                        },
                        onUserStoppedTyping: user => {
                            this.setState({
                                usersWhoAreTyping: this.state.usersWhoAreTyping.filter(username => username !== user.name)
                            })
                        },
                        onUserCameOnline: () => this.forceUpdate(),
                        onUserWentOffline: () => this.forceUpdate(),
                        onUserJoined: () => this.forceUpdate(),
                    }
                })
            })
            .then(currentRoom => {
                this.setState({ currentRoom })
            })
            .catch(error => console.error(error))
    }

    render() {
        return (
            <div style={{
                display: 'flex',
                height: '100vh',
                backgroundColor: '#F8F8F9'
            }}>
                <div className="sidebar">
                    <WhosOnlineList users={this.state.currentRoom.users} />
                </div>
                
                <div className="message-view">
                    <div style={{
                        flex: 1,
                        width: "100%"
                    }}>
                        <MessageList messages={this.state.messages}/>
                    </div>
                    
                    <TypingIndicator usersWhoAreTyping={this.state.usersWhoAreTyping} />
                    <SendMessageForm 
                        onSubmit={this.sendMessage} 
                        onChange={this.sendTypingEvent}
                    />
                </div>
            </div>
        )
    }
}

export default Chatscreen;